@font-face {
  font-family: 'icons';
  src: url('icons.eot'); /* IE9 Compat Modes */
  src: url('icons.eot?iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('icons.woff2') format('woff2'), /* Edge 14+, Chrome 36+, Firefox 39+, some mobile */
    url('icons.woff') format('woff'),  /* IE 9+, Edge, Firefox 3.6+, Chrome 5+, Safari 5.1+ */
    url('icons.ttf') format('truetype'), /* Safari, Android, iOS */
    url('icons.svg') format('svg'); /* Legacy iOS */
  font-weight: normal;
  font-style: normal;
}
[class^="aicon-"], [class*=" aicon-"] {
  font-family: 'icons' !important; /* Use !important to prevent issues with browser extensions that change fonts */
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  text-rendering: optimizeSpeed; /* Kerning and ligatures aren't needed */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
